import React from "react";
import style from "./StaffIdCard.module.css";
import IDCardSetting from "../../../../StudentTab/MainContent/IDCards/IDCardSetting";
import { useSelector } from "react-redux";

function StaffIdCard() {
  const ids = useSelector((state) => state.idChange);

  return (
    <div className={style.staffidcardcontainer}>
      <IDCardSetting instituteId={ids?.id} status="staff" />
    </div>
  );
}

export default StaffIdCard;
